
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'


export default {
data() {
return {
userMobile: Lockr.get('userInfo').mobile,
higher: {
name: '',
mobile: ''
}
}
},
components: {
Group,
Cell,
XButton
},
methods: {
unblind() {
const that = this
/**
* 用户解绑
*/
that.$vux.confirm.show({
content: '每个账户只有一次解绑机会\n是否确认解除绑定',
confirmText: '解除绑定',
cancelText: '不解除',
onCancel () {},
onConfirm () {
api.post('/invite/unbind', {}).then(data => {

that.$vux.toast.show({
text: '解绑成功',
type: 'success'
})

that.higher = {
name: '',
mobile: ''
}
})
}
})
}
},
created: function () {
const that = this


document.title = '个人资料'

/**
* 获取用户邀请人信息
*/
api.get('/invite/getInviterInfo', {}).then(data => {
if(data) {
const origin_type = data.origin_type


switch (origin_type) {
case 1:

that.higher = {
name: '',
mobile: data.user.mobile
}
break
case 2:

that.higher = {
name: data.merchant.name,
mobile: data.mappingUser ? ('(' + data.mappingUser.mobile + ')') : ''
}
break
case 3:

that.higher = {
name: data.oper.name,
mobile: data.mappingUser ? ('(' + data.mappingUser.mobile + ')') : ''
}
break
default:
that.higher = {
name: '',
mobile: ''
}
break
}
}
}).catch((msg) => {
if(msg.response.code == 500) {
that.higher = {
name: '',
mobile: ''
}
}
})
},
mounted () {
}
}



