
import { mapState } from 'vuex'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'


export default {
data() {
/**
* form：       表单
* mobile：     手机号
* verifyCode： 验证码
* showLogin：  展示登录界面
* codeLock：   发送验证码锁
* countDown：  倒计时
* interval：   计时器
*/
return {
form: {
mobile: '',
verifyCode: '',
},
showLogin: false,
codeLock: false,
countDown: 60,
interval: null
}
},
components: {
Group,
XInput,
XButton
},
computed: {
...mapState([
'projectName',
'systemName',
'user'
])
},
methods: {
/**
* 跳转页面：如果有指定，则跳到指定页面，否则返回首页
*/
relocation() {

if (this.$route.query && this.$route.query._from) {
router.push(this.$route.query._from);
} else {
router.push('/');
}
},
/**
* 发送验证码：手机号不能为空，发送成功后有60秒的倒计时
*/
sendCode() {
const that = this
const mobile = that.form.mobile.replace(/\s/g, '')


if(mobile != '') {
api.post('/sms/verify_code', {mobile: mobile}).then(data => {

that.codeLock = true
that.countDown = 60
that.interval = setInterval(() => {

if(that.countDown == 0) {
that.codeLock = false
clearInterval(that.interval)
return
}

that.countDown = that.countDown - 1
}, 1000)
}).catch((msg) => {}).finally((msg) => {})
} else {
that.$vux.toast.show({
text: '手机号不能为空',
type: 'warn'
})
}
},
/**
* 登录：手机号和验证码都不能为空，才能登录。表单数据进行去空格处理
*/
doLogin() {
const that = this


const param = {
mobile: that.form.mobile.replace(/\s/g, ''),
verify_code: that.form.verifyCode.replace(/\s/g, '')
}

if(param.mobile != '' && param.verify_code != '') {

api.post('/login', param).then(data => {
store.dispatch('storeUserInfo', data)
that.relocation()
}).catch((msg) => {}).finally((msg) => {})
}
}
},
created: function () {
if(this.user) {
this.$vux.toast.show({
text: '您已登录',
type: 'warn'
})
router.go(-1)
return
}


document.title = '注册/登录'
},
mounted () {

const that = this;
that.showLogin = true;
}
}



