import axios from 'axios'
import AlertModule from 'vux/src/plugins/alert/module'


/**
* 正式：https://o2o.niucha.ren/api/app/user/
* 测试：https://xiaochengxu.niucha.ren/api/app/user/
*/

window.baseApiUrl = 'https://o2o.niucha.ren/api/app/user/'
const CODE_OK = 0
const CODE_UN_LOGIN = 10003

class ResponseError {
constructor(response) {
this.response = response
}
}

/**
* 处理请求返回
*
* 1. 如果请求回调存在，且状态码为0，则返回数据
* 2. 否则，返回统一的请求错误：可以使用promise模式
*/
function handlerRes(res) {
if (res && res.code === CODE_OK) {
return res.data
} else {
return Promise.reject(new ResponseError(res))
}
}

/**
* 处理返回错误
*
* 1. 如果错误类型属于ResponseError（自定义请求错误返回），
*  1.1 则先检查是否有请求返回数据且包含状态码，
*     1.1.1 如果状态码为10003，则是登录请求错误，返回对应的错误
*     1.1.2 其他状态码则统一返回接口错误信息
*  1.2 否则返回未知错误
* 2. 否则返回网络错误
*/
function handlerError(error) {
if(error instanceof ResponseError) {
let res = error.response

if(res && res.code) {
switch (res.code) {
case CODE_UN_LOGIN:
Lockr.rm('userInfo')
Lockr.rm('menus')
router.push('/login')
AlertModule.show({
content: '您的登录信息已失效, 请先登录'
})
break
default:
console.log('接口返回错误信息:', res)
if (!res.disableErrorMessage) {
AlertModule.show({
content: res.message
})
}
break
}
} else {
console.log('未知错误:', res)
}
} else {
console.error('network error: ', error)
AlertModule.show({
content: res.message
})
}
}

/**
* 处理请求地址
*
* 1. 如果地址已经包含有基本的api地址前缀，则直接返回该地址
* 2. 如果地址开头有“/”，则去除“/”，便于后面和基本api地址合并
* 3. 返回默认api和url合并的地址
*/
function getRealUrl(url) {
if(url.indexOf(window.baseApiUrl) === 0){
return url
}
if(url.indexOf('/') === 0) {
url = url.substr(1)
}
return window.baseApiUrl + url
}

/**
* 自定义get请求
*
* param：
*  url：请求地址
*  params：请求参数
*  defaultHandlerRes：默认处理请求返回数据
*
* 1. 请求头设置请求格式（XMLHttpRequest）和token
* 2. 通过axios请求，如果“默契处理请求返回数据”，则使用handlerRes处理数据。否则直接返回数据
* 3. 由于axios使用promise，则利用promise获取请求错误，使用handlerError处理请求错误
* 4. 返回请求的promise
*/
function get(url, params, defaultHandlerRes=true) {
let options = {
headers: {'X-Requested-With': 'XMLHttpRequest', 'token': Lockr.get('token') },
params: params,
}
url = getRealUrl(url)
let promise = axios.get(url, options).then(res => {
let result = res.data
if(defaultHandlerRes) {
return handlerRes(result)
} else {
return result;
}
});
promise.catch(handlerError)
return promise
}

/**
* 同get
*/
function post(url, params, defaultHandlerRes=true) {
let options = {
headers: { 'X-Requested-With': 'XMLHttpRequest', 'token': Lockr.get('token') }
}
url = getRealUrl(url)
let promise = axios.post(url, params, options).then(res => {
let result = res.data

if(defaultHandlerRes) {
return handlerRes(result)
} else {
return result
}
});
promise.catch(handlerError)
return promise
}

/**
* 使用promise处理数据，返回promise
*/
function mockData(data) {
return new Promise((resolve, reject) => {
resolve(data)
})
}

export default {
get,
post,
mockData,
handlerRes,
}


