import './global'



import Vue from 'vue'
import FastClick from 'fastclick'
import App from './App'


import ToastPlugin from 'vux/src/plugins/toast/index.js'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'

Vue.use(ToastPlugin)
Vue.use(ConfirmPlugin)

import store from './store'
window.store = store

import VueRouter from 'vue-router'
import routes from './router/index'

/**
* router的构建选项
*
*  base：应用的基路径
*  mode：配置路由模式（hash：以URL的hash值来做路由。history：依赖h5的hisitory和服务器配置。abstract：支持JavaScript的所有运行环境。）
*/
const router = new VueRouter({
base: '/user-h5',
mode: 'history',
routes
})

/**
* 利用router的before和after改变loding状态
*/
router.beforeEach(function (to, from, next) {
store.commit('updateLoadingStatus', { isLoading: true })
next()
})

router.afterEach(function (to) {
store.commit('updateLoadingStatus', { isLoading: false })
})

Vue.use(VueRouter)
window.router = router


FastClick.attach(document.body)


Vue.config.productionTip = false


import api from './assets/js/api'
window.api = api

/* eslint-disable no-new */
new Vue({
router,
store,
render: h => h(App)
}).$mount('#app-box')



